<template>
  <div id="music">
    <Row class="row" v-for="playlist in playlistList">
      <Button class="button" long size="large" type="primary" @click="setPlaylist(playlist)">
        {{playlist.name}}
      </Button>
    </Row>
  </div>
</template>

<script>
  export default {
    name: "Music",
    data() {
      return {
        playlistList: [
          {
            name: '周杰伦',
            artistList: ['周杰伦']
          },
          {
            name: '林俊杰',
            artistList: ['林俊杰']
          },
          {
            name: '古风',
            artistList: ['韶华', 'Ace', 'HITA', 'SING女团', '阿兰', '初夏小溪', '黄睿铭', '绘梨衣', '井宿', '林斜阳',
              '刘珂矣', '少司命', '司夏', '天時', '熙er', '心然', '徐秉龙', '许多葵', '鸦青', '诗听', '苑舍']
          },
          {
            name: '治愈女声',
            artistList: ['SING女团', '阿泱', '阿颖', '本兮', '郭静', '何曼婷', '金莎', '锦零', '任然', '王心凌', '张碧晨'],
            include: ['古风']
          },
          {
            name: '欧美',
            artistList: [
              'Carpenters', 'Dion', 'Emilia', 'John Legend', 'Laura', 'Linkin Park', 'Richie', 'Michael', 'Miley',
              'Richard Marx', 'Secret Garden', 'Selena', 'Take Me', 'Westlife', 'Whitney', 'Khalifa']
          },
          {
            name: '民谣',
            artistList: ['陈鸿宇', '陈粒', '陈小熊', '陈雪凝', '贰佰', '房东的猫', '马頔', '宋冬野', '王威', '赵雷']
          },
          {
            name: '摇滚',
            artistList: ['崔健', '李克勤', '汪峰', '伍佰', '许巍', '赵钶']
          },
          {
            name: '漂泊',
            artistList: ['朴树', '任贤齐', '王强'], include: ['民谣', '摇滚']
          },
          {
            name: '经典',
            artistList: ['Beyond', '阿杜', '蔡健雅', '陈奕迅', '林志炫', '罗志祥', '那英', '群星', '水木年华',
              '孙燕姿', '逃跑计划', '王菲', '王力宏', '许嵩', '张惠妹', '张靓颖', '张韶涵', '张信哲', '张学友', '张震岳', '周传雄', '周华健']
          },
          {
            name: '古筝',
            artistList: ['古筝'],
          },
          {
            name: '轻音乐',
            include: ['古筝'],
          }
        ]
      }
    },
    methods: {
      setPlaylist: function (data) {
        let allPlaylist = this.$store.state.allPlaylist;
        let playlist = [];

        function add(music) {
          playlist.forEach((item) => {
            if (item.src === music.src) {
              return false;
            }
          });
          playlist.push(music);
          return true;
        }

        let addPlaylist = (data) => {
          if (data.artistList !== undefined) {
            allPlaylist.forEach((music) => {
              for (let artist of data.artistList) {
                if (music.artist.indexOf(artist) !== -1) {
                  add(music);
                }
              }
            })
          }
          if (data.include !== undefined) {
            data.include.forEach((playlistIncludedName) => {
              this.playlistList.forEach((playlistIncluded) => {
                if (playlistIncluded.name === playlistIncludedName) {
                  addPlaylist(playlistIncluded);
                }
              })
            })
          }
        }

        addPlaylist(data);

        console.log(playlist);
        if (playlist !== []) {
          this.$store.commit('setPlaylist', playlist);
        }
      }
    }
  }
</script>

<style scoped lang="sass">
  .row
    margin-bottom: 1rem

  .button
    height: 3rem
</style>



